.backdrop{
    position: fixed; /*to define the position of element in a document*/
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 20;  /*for overlapping html elements*/
    background-color: rgba(0,0,0,0.75);
    
  }
  
